@import url(https://fonts.googleapis.com/css?family=Rubik&display=swap);
.navbar {
  box-shadow: -2px -1px 20px 0px;
  border-radius: 0.5em;
}

.navbar-light .navbar-nav .nav-link {
  font-size: 1.5em;
  margin-left: 2em;
}

.navbar-brand {
  font-size: 2em !important;
}
.products .row {
  padding-left: 2em;
}
.products--title {
  position: fixed;
  font-size: 2em;
  top: 3em;
  left: 0;
}
.products--title span {
  display: block;
  padding: 0 0.5em;
}

.card {
  box-shadow: -2px -1px 20px 0px;
  margin-top: 2em;
  background-color: lavender;
  border: none;
}
.basked {
  position: relative;
  margin-top: 2em;
}
.basked--content {
  position: -webkit-sticky;
  position: sticky;
  height: 80vh;
  overflow-y: auto;
  top: 0;
}
.basked--content--title {
  text-align: center;
  padding: 1em;
}
.basked--content--title i {
  font-size: 2em;
}
.basked--content--title span {
  font-size: 1.5em;
  margin-left: 1em;
}
.basked--content--table--body button {
  background-color: transparent;
  border: none;
}
.basked--content--table--body button :focus {
  outline: 0;
}
.basked--content--table--body button i {
  color: #22411f;
}
.basked--content--buttonsSend {
  display: grid !important;
}
.basked--content--buttonsSend button {
  margin-top: 0.7em;
}
.basked--content--response {
  padding: 1em 0;
}
.App {
  position: relative;
  background-color: lavender;
  font-family: "Rubik", sans-serif !important;
}

.btn {
  background-color: #275622e0;
  border-color: #275622e0;
}
.btn:hover {
  background-color: #fff;
  color: #275622e0;
  border-color: #22411f;
}

@media only screen and (max-width: 576px) {
  .row {
    justify-content: center;
  }
}
